	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by noboru-i","subtitle":"noboru-iのメモ的な何かです。","copyright":"© All rights reserved.","author":{"name":"noboru-i","twitter":"noboru_i"},"disqusShortname":"","url":"https://noboru-i.github.io/"}},"markdownRemark":{"id":"/home/circleci/code/src/pages/articles/2018/2018-05-11.md absPath of file >>> MarkdownRemark","html":"<h2>Android App Bundle/Dynamic feature modulesにみるモジュール化の未来 - tomoima525’s blog</h2>\n<p><a href=\"http://tomoima525.hatenablog.com/entry/2018/05/10/092208\">http://tomoima525.hatenablog.com/entry/2018/05/10/092208</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/android/\">Android</a>\n  </li>\n</ul>\n<p>Google I/O 2018で発表された、apkサイズの削減のための仕様をまとめたもの。</p>\n<p>個人アプリでNDKを使ったものもあるので、これを試してみるのが良さそうです。\nまた、Store側の処理になるので、対象となるOSバージョンが広そうなのが良いですね。</p>\n<p>また、internal testもすぐに公開してくれるのであれば、DeployGateの代わりに出来るかも？ちょっと試してみる必要がありそうですね。</p>\n<h2>Google I/O Developer Keynote Android開発のまとめ | TechBooster</h2>\n<p><a href=\"https://techbooster.org/android/18096/\">https://techbooster.org/android/18096/</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/android/\">Android</a>\n  </li>\n</ul>\n<blockquote>\n<p>Android App Bundle</p>\n</blockquote>\n<p>この前、keystoreをPlay consoleにアップロードする仕組みが出来てたけど、これの布石だったのかな。\nたしかに、ダウンロードするapkの中に、他のアーキテクチャ用のバイナリとか必要ないよねー。</p>\n<blockquote>\n<p>Android Jetpack</p>\n</blockquote>\n<p>こっちは、AndroidStudioにかなり機能が増えそうな印象。</p>\n<blockquote>\n<p>Android Slices</p>\n</blockquote>\n<p>また、Android開発者が覚えるべき知識・機能が増えたのね。。。ぐらいの印象です。</p>\n<h2>Speaker Deck で最高の URL を生成する技術 - kakakakakku blog</h2>\n<p><a href=\"http://kakakakakku.hatenablog.com/entry/2018/05/07/003042\">http://kakakakakku.hatenablog.com/entry/2018/05/07/003042</a></p>\n<ul class=\"post-single__tags-list\">\n</ul>\n<p>Speaker DeckのURL発行の仕組みについて。</p>\n<p>今まで、気にはなっていたけど何もしてなかったので、今度アップロードすることがあれば、意識してやってみようと思います。</p>\n<h2>mikutterのTwitterコンシューマキーが凍結されました - mikutter blog</h2>\n<p><a href=\"http://mikutter.hatenablog.com/entry/2018/05/05/164014\">http://mikutter.hatenablog.com/entry/2018/05/05/164014</a></p>\n<ul class=\"post-single__tags-list\">\n</ul>\n<p>Twitterのコンシューマキーが凍結されてしまったので、その後の対応など。\n斜め読みしかしてないですが。。</p>\n<p>テスト環境用のTwitterアプリを、自分のアカウントで発行したりすることがあったんですが、今後は捨てアカウントとかで取ったほうが安全かもしれませんね。。（マルチアカウントの問題は別途あるかもしれないので、そこは要確認）</p>","fields":{"tagSlugs":["/tags/android/"]},"frontmatter":{"title":"Weekly news at 2018/05/11","tags":["Android"],"date":"2018-05-11T17:01:24+00:00","description":"2018/05/11のウィークリーニュース"}}},"pathContext":{"slug":"/posts/weekly-news/2018/05/11/"}}

